.home {
    background: linear-gradient(124.14deg, #ACD5EB 9.63%, rgba(172, 213, 235, 0) 180.6%)!important;
}
.jumbotron-text-container{
    max-width: 607px;
    margin: 23vh 0 0 72px ;
}
.jumbotron-text {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 72px;
    line-height: 88px;
}

.jumbotron-image {
    width: 465px;
    margin-left: 100px;
}

@media screen and (max-width:960px) {
    .jumbotron-text-container{
        max-width: 200px;
        margin: 30vh 0 0 0px ;
    }
    .jumbotron-text{
        font-size: 20px;
        line-height: inherit;
    }
    .jumbotron-image {
        width: 47vw;
        margin-top: 15vh;
        margin-left: 20px;
    }
}