.App {
  text-align: center;
}

* {
  margin:0;
  padding:0;
  font-family: 'Noto Sans JP', sans-serif;
}

.container {
  display: flex;
  margin: 0 0 0 10%;
  width: 80%;
  flex-direction: column;
}

.row {
    display: flex;
    flex-direction: row;
    width: 100%;
    padding:10px;
    margin: 10px;
}
.col {
    display: flex;
    margin: 10px 10px;
    flex-direction: column;
    /* width: auto; */
}

.section {
  height: 100vh;
  background-color: rgb(245, 245, 245);
}

.section:first-child{
  padding-top: 10vh;
  height: 90vh;
}

@media screen and (max-width:960px) {
  .row {
    padding:0;
    margin:0;
  }
  
}
