:root {
    --bg: #242526;
    --nav-size: 60px;
    --text-color: #dadce1;
    --border: 1px solid #474a4d;
    --border-radius: 8px;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    color: var(--text-color);
    text-decoration: none;
}

.navbar {
    width: 100%;
    position:absolute;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* margin: 0 10px 10vh 10px !important;
    padding: 0 0 10vh 0 !important; */
}

.nav-logo {
    font-size: 2.1rem;
    font-weight: 500;
    color: #482ff7;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 98vw;
    justify-content: end;
    margin-right: 2rem;
  }


.nav-item {
    color:#000;
    margin: 24px 5px;
    padding: 14px;
    transition: 0.3s;
    border-radius: 10px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
}

.nav-item span{
    transition: 0.1s;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
}


.nav-item-accent {
    transition: .3s;
    color:#4f7296;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
}

.nav-item:hover {
    background-color: #4f7296;
    color: #fff;
    transition: .5s;
}

.menu-icon {
    display: none;
    color: #fff;
}

@media screen and (max-width:960px) {
    .navbar {
        position: relative;
        background-color: rgb(130, 197, 223);
    }

    .nav-logo {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(25%, 50%);
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 90vh;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        background-color: rgb(130, 197, 223);
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-item:hover{
        color: aqua;
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transition: 1s;
        transform: translate(-100%, 60%);
        font-size: 1.5em;
        cursor: pointer;
    }
}